.browse-container .banner-img {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  min-height: -webkit-fill-available;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.3s;
  background-color: black;
  -webkit-background-size: cover;
}
.browse-container .test-bg {
  position: relative;
  height: 100px;
  margin-top: -70px;
  /* background-color: #141414; */
  background: linear-gradient(0deg, #141414 0%, #141414 50%, rgba(20, 20, 20, 0) 100%);
}
